div[class*="language-"] {
    background-color: rgb(43, 43, 43);
    color: rgb(169, 183, 198);

    font-family: 'JetBrains Mono', monospace;
    font-variant-ligatures: none;
    font-style: normal;
    font-weight: 300;
    font-size: min(3.6vw, 15px);

    line-height: 1.5em;

    display: flex;
    flex-direction: column;

    border-radius: 10px;

    position: relative;

    -moz-tab-size: 4;
    tab-size: 4;

    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;

    overflow: hidden;
}

div[class*="language-"] .overlay {
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 10px;
    align-items: baseline;

    transition: 0.125s;
    color: rgb(100, 100, 100);

    padding: 10px;
    position: sticky;
    right: 0;
}

div[class*="language-"] .overlay:hover {
    cursor: pointer;
    color: gainsboro;
}

.post.mobile div[class*="language-"] .overlay:hover {
    color: rgb(100, 100, 100);
}

.code-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    padding: 10px;

    background-color: rgb(56, 56, 56);
    color: rgb(128, 128, 128);

    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.code-header.no-banner {
    position: absolute;
    right: 0;
    background-color: rgb(0,0,0,0);
}

.code-header span {
    display: flex;
    /*align-items: end;*/
    align-items: center;
    padding-left: 18px;
    padding-right: 18px;
}

.code-block {
    display: flex;
    flex-direction: row;

    padding-top: 18px;
    padding-bottom: 18px;

    overflow: auto;
}

.code-block::-webkit-scrollbar-thumb {
    background-color: rgb(100, 100, 100);
}

.copy-button {
    color: rgb(100, 100, 100);
    transition: 0.1s color;
    padding: 6px;

    line-height: 1em;

    display: flex;
    flex-direction: row;
    justify-content: center;
}

.copy-button:hover {
    cursor: pointer;
    color: rgb(128, 128, 128);
}

.copy-button i {
    margin: auto 0;
    font-size: 20px;
}

.copy-button span {
    color: rgb(100, 100, 100);
    font-size: 14px;
    padding: 0 18px 0 18px;
}

.metadata {
    display: flex;
    flex-direction: column;

    color: rgb(93, 96, 99);

    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.metadata div {
    display: flex;
    flex-direction: row;
}

.metadata .line-number {
    padding: 0;
    margin: 0;
}

.metadata .separator {
    border-width: 1px;
    border-right-style: solid;
}

.metadata .padding {
    /* Needs to match .code padding */
    padding-left: 9px;
    padding-right: 9px;
}

.code {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    padding-left: 18px;
    padding-right: 18px;
}

/* Code blocks with diff elements have their padding handled by the diff symbol */
.code.diff {
    padding-left: 0;
}

.code code {
    display: flex;
    flex-direction: row;
}

.added {
    background-color: rgb(41, 68, 54);
}

.removed {
    background-color: rgb(88, 49, 49);
}

.modified {
    background-color: rgb(75, 74, 54);
}

.highlighted {
    background-color: rgb(45, 74, 88);
    width: 100%;
}

/* SECTION: Language syntax highlighting ------------------------------------------------------ */

.language-cpp .comment {
    color: rgb(128, 128, 128);
}

.language-cpp .keyword,
.language-cpp .boolean,
.language-cpp .semicolon {
    color: rgb(206, 136, 70);
}

.language-cpp .string,
.language-cpp .char {
    color: rgb(106, 171, 115);
}

.language-cpp .preprocessor-directive {
    color: rgb(187, 181, 41);
}
.language-cpp .macro-name,
.language-cpp .macro-argument {
    color: rgb(149, 144, 51);
}

.language-cpp .macro.directive-hash,
.language-cpp .macro.directive {
    color: rgb(187, 181, 41);
}
.language-cpp .macro.macro-name {
    color: rgb(144, 139, 37);
}
.language-cpp .macro.expression {
    color: rgb(108, 106, 42);
}

.language-cpp .function {
    color: rgb(255, 198, 109);
}

.language-cpp .class-name,
.language-cpp .namespace-name,
.language-cpp .enum-name,
.language-cpp .concept {
    color: rgb(181, 182, 227);
}

.language-cpp .enum-value {
    color: rgb(199, 125, 187);
}

.language-cpp .double-colon {
    color: rgb(169, 183, 198);
}

.language-cpp .number,
.language-cpp .symbol {
    color: rgb(42, 172, 184);
}

.language-cpp .member-variable {
    color: rgb(152, 118, 170);
}

.language-cpp .operator,
.language-cpp .unary-operator,
.language-cpp .binary-operator,
.language-cpp .function-operator {
    color: rgb(95, 140, 138);
}

.language-cpp .undefined {
    color: rgb(104, 106, 78);
}